.UserDialog-Wrapper {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background: rgba(0, 0, 0, 0.3);
    z-index: 10;
}

.UserDialog {
    border: 1px solid #000;
    border-radius: 4px;
    min-width: 20em;
    background: #fff;
    -webkit-box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
            box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
}

.UserDialog > nav:first-child {
    padding: 1em;
}

.UserDialog > nav:first-child input[type=radio]:not(:first-child) {
    margin-left: 2em;
}

.UserDialog > .panes {
    padding: 0 1em 1em;
}

.UserDialog form > .row {
    display: -ms-flexbox;
    display: flex;
    padding: .3em 0;
}

.UserDialog form > .row > label:first-child {
    width: 4em;
}

.UserDialog form > .row.actions {
    padding-left: 4em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.UserDialog form > .row.actions button {
    margin-right: .5em;
}

.UserDialog .forgotPassword > h3 {
    padding: 1em;
}

.UserDialog .forgotPassword > form {
    padding: 0 1em 1em;
}

.UserDialog button {
    font: inherit;
    padding: .2em;
}

.UserDialog > nav:first-child {
    padding: 1em;
    margin-left: -2em;
}

.UserDialog > nav:first-child label {
    margin-left: 2em;
    cursor: pointer;
}


